<template>
  <div class="home">
    <el-container class="home2">
      <el-main>
        <about class="about" ref="about"></about>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import About from '@/components/common/About'

  export default {
    name: 'AppIndex',
    components: {About}
  }
</script>

<style scoped>
  .home {
    /*width: 990px;*/
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #e7f0fd;
  }
  .home2{
    position: absolute;
    left: 60%;
    top:50%;
    transform: translate(-50%,-50%);
  }
  .starter {
    width: 990px;
    height: 2000px;
    margin-left: auto;
    margin-right: auto;
  }
</style>



